import React from "react";

interface NavBarProps {
  userName: string;
  onLogout: () => void;
  cameraStatus: "off" | "starting" | "on";
}

const cameraStatusColors: Record<NavBarProps["cameraStatus"], string> = {
  off: "bg-red-500",
  starting: "bg-yellow-500",
  on: "bg-green-500",
};

export default function NavBar({
  userName,
  onLogout,
  cameraStatus,
}: NavBarProps) {
  return (
    <nav className="flex items-center justify-between px-6 py-3 shadow bg-white">
      {/* Left: Logo + Title */}
      <div className="flex items-center space-x-2">
        <span className="text-xl font-bold text-gray-800">StressSense</span>
      </div>

      {/* Middle: Camera status */}
      <div className="flex items-center space-x-2">
        <span
          className={`w-3 h-3 rounded-full ${cameraStatusColors[cameraStatus]}`}
        />
        <span className="text-sm text-gray-600 capitalize">
          {cameraStatus === "on"
            ? "Camera On"
            : cameraStatus === "starting"
            ? "Starting..."
            : "Camera Off"}
        </span>
      </div>

      {/* Right: User + Logout */}
      <div className="flex items-center space-x-4">
        <span className="text-gray-700">Hi, {userName}</span>
        <button
          onClick={onLogout}
          className="px-3 py-1 text-sm bg-gray-100 text-gray-700 rounded hover:bg-gray-200"
        >
          Logout
        </button>
      </div>
    </nav>
  );
}
